<template>
<div>
  <h1 class="page-header">Notes</h1>
  <div id="newBtn_container">
    <router-link to="/"><i class="fas fa-home"></i></router-link>
    <button id="newBtn" @click="add = !add"><i class="fas fa-plus"></i></button>
  </div>

  <div id="createNewNote" v-if="add === true">
    <h5 class="new-heading">Add Note</h5>
    <form class="new_note_form form-group col-md-12">
      <input type="text" name="name" class="form-control mb-2 new_note_text col-md-6" v-model="title" placeholder="new note">
      <div id="btn_div" class="col-sm-12 col-md-6">
        <button class="btn btn-success col-sm-6 mt-2" type="submit" @click.prevent="save_note">Save</button>
        <button class="btn btn-cancel col-sm-6 mt-2" @click.prevent="cancel_add_note">Cancel</button>
      </div>
    </form>
  </div>
  <div id="notes_container" class="fade-in slide-in-bottom">
    <ul>
      <li v-for="note in notes" :key="note.id">
        <div class="notes_list">
          <h5 class="single_note">{{ note.title }}</h5>
          <button @click="remove_note(note.id)"><i class="fas fa-trash icon-trash fade-in2"></i></button>
        </div>
      </li>
    </ul>
  </div>
</div>

</template>
<script>
import { db } from "@/config/db.js";

export default {
  name: "notes",
  data() {
    return {
      add: false,
      title: "",
      notes: []
    };
  },
  methods: {
    save_note() {
      if (this.title) {
        db.collection("Notes").add({
          title: this.title.charAt(0).toUpperCase() + this.title.slice(1),
          user_id: localStorage.getItem("myorganiser_userId")
        });
      }
      this.title = "";
    },
    cancel_note() {
      this.add = false;
      this.title = "";
    },
    remove_note(id) {
      db
        .collection("Notes")
        .doc(id)
        .delete();
    },
    cancel_add_note() {
      this.title = "";
    }
  },
  firestore() {
    return {
      notes: db
        .collection("Notes")
        .where("user_id", "==", localStorage.getItem("myorganiser_userId"))
    };
  },
  beforeMount() {
    if (!this.$store.getters.getLoggedStatus) {
      this.$router.push("/");
    }
  }
};
</script>
